<span class="hljs-keyword">this</span>.$vux.confirm.show({
  <span class="hljs-comment">// 组件除show外的属性</span>
  onCancel () {
    <span class="hljs-built_in">console</span>.log(<span class="hljs-keyword">this</span>) <span class="hljs-comment">// 非当前 vm</span>
    <span class="hljs-built_in">console</span>.log(_this) <span class="hljs-comment">// 当前 vm</span>
  },
  onConfirm () {}
})
<span class="hljs-comment">// 隐藏</span>
<span class="hljs-keyword">this</span>.$vux.alert.hide()
</code></pre>
