</code></pre>
<p><code>Group</code>是一个特殊的表单<code>wrapper</code>组件，主要用于将表单分组，单个表单元素也算一组。常见的表单组件都<code>必须</code>作为<code>Group</code>的子组件。 属于<code>Group</code>子组件的有：Cell, XInput, XTextarea, XSwitch, Calendar, XNumber, Radio, XAddress, Datetime, Selector</p>
