</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">labelWidth</span></td>
<td><span class="type type-string">String</span></td>
<td></td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>为子元素设定统一label宽度</td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">labelAlign</span></td>
