<h3>Picker_COM</h3>
<p><span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/components/picker/metas.yml" target="_blank">编辑文档</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="#" router-link="/zh-CN/demos/picker">进入demo页面</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://vux.li/demos/v2/#/component/picker" target="_blank">demo 原始链接</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/demos/Picker.vue" target="_blank">demo源码</a></span></p>
<pre class="hljs"><code><span class="hljs-keyword">import</span> { Picker } <span class="hljs-keyword">from</span> <span class="hljs-string">'vux'</span>
</code></pre>
<p class="warning">
  请确保列表项的`value`值是字符串，使用数字会出错。
  <br />
  如果你的业务接口返回数字值为数字，需要你先处理成数字；同样，获取到值时为字符串，你需要自己转换成数字。
</p>
<p>非联动情况下，列表数据格式示例：</p>
<pre class="hljs"><code><span class="hljs-comment">// data</span>
[[<span class="hljs-string">'小米'</span>, <span class="hljs-string">'iPhone'</span>, <span class="hljs-string">'华为'</span>, <span class="hljs-string">'情怀'</span>, <span class="hljs-string">'三星'</span>, <span class="hljs-string">'其他'</span>, <span class="hljs-string">'不告诉你'</span>], [<span class="hljs-string">'小米1'</span>, <span class="hljs-string">'iPhone2'</span>, <span class="hljs-string">'华为3'</span>, <span class="hljs-string">'情怀4'</span>, <span class="hljs-string">'三星5'</span>, <span class="hljs-string">'其他6'</span>, <span class="hljs-string">'不告诉你7'</span>]]

<span class="hljs-comment">// 或者使用 name =&gt; value 的形式</span>
[[{
  <span class="hljs-attr">name</span>: <span class="hljs-string">'2019届5班'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'1'</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'2019届4班'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'2'</span>
}]]

<span class="hljs-comment">// value</span>
[<span class="hljs-string">'小米'</span>, <span class="hljs-string">'小米1'</span>]
</code></pre>
<p>联动时，列表数据格式示例：</p>
<pre class="hljs"><code><span class="hljs-comment">// data</span>
[{
  <span class="hljs-attr">name</span>: <span class="hljs-string">'中国'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'china'</span>,
  <span class="hljs-attr">parent</span>: <span class="hljs-number">0</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'美国'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'USA'</span>,
  <span class="hljs-attr">parent</span>: <span class="hljs-number">0</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'广东'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'china001'</span>,
  <span class="hljs-attr">parent</span>: <span class="hljs-string">'china'</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'广西'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'china002'</span>,
  <span class="hljs-attr">parent</span>: <span class="hljs-string">'china'</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'美国001'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'usa001'</span>,
  <span class="hljs-attr">parent</span>: <span class="hljs-string">'USA'</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'美国002'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'usa002'</span>,
  <span class="hljs-attr">parent</span>: <span class="hljs-string">'USA'</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'广州'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'gz'</span>,
