<h3>Scroller_COM</h3>
<p><span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/components/scroller/metas.yml" target="_blank">编辑文档</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="#" router-link="/zh-CN/demos/scroller">进入demo页面</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://vux.li/demos/v2/#/component/scroller" target="_blank">demo 原始链接</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/demos/Scroller.vue" target="_blank">demo源码</a></span></p>
<pre class="hljs"><code><span class="hljs-keyword">import</span> { Scroller } <span class="hljs-keyword">from</span> <span class="hljs-string">'vux'</span>
</code></pre>
<p class="warning">
`Scroller`的内容必须是一个`div`，并且只能有一个`div`
<br>
`Scroller` 希望解决的是简单的列表问题而不是一个内嵌各种复杂标签交互的容器，很容易发生性能或者交互问题。
<br>
<br>
默认高度为整个视口高度，如果你加了`x-header`，那么你需要减去一个`x-header`的高度: `height="-46"`
<br>
<br>
请确保在你的数据更新后进行`reset`操作(参考下面文档)，如果你做了`reset`还有问题再开`issue`并附上代码，否则将`绝对不会被处理`。
</p>
