</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">on-scroll-bottom</span></td>
<td> </td>
<td>滚动到底部时触发，注意事件会触发多次，如果你需要进行数据获取，记得设置一个状态值</td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">on-pulldown-loading</span></td>
<td> </td>
<td>用户触发下拉刷新状态，监听该事件以获取加载新数据</td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">on-pullup-loading</span></td>
<td> </td>
<td>用户触发上拉加载状态，监听该事件以加载新数据</td>
</tr>
</tbody>
</table>
<p><span class="vux-props-title">Methods</span></p>
<table>
<thead>
<tr>
<th>name</th>
<th>params</th>
<th>description</th>
<th>version</th>
</tr>
</thead>
<tbody>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">reset</span></td>
<td><code>(position, duration, easing)</code></td>
<td>在内容变化(v-for渲染，异步数据加载)后需要调用，用以重新渲染，避免新加的内容无法上拉看到，一般在 $nextTick 回调里调用。easing 可以为 ease-in, ease-in-out, ease, bezier(n, n, n, n)</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">donePullup</span></td>
<td> </td>
<td>设置上拉刷新操作完成，在数据加载后执行</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">disablePullup</span></td>
<td> </td>
<td>禁用上拉刷新，在没有更多数据时执行</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">enablePullup</span></td>
<td> </td>
<td>启用上拉刷新插件</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">donePulldown</span></td>
<td> </td>
<td>设置下拉刷新操作完成，在数据加载后执行</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
</tbody>
</table>
