<p>pulldown 默认配置:</p>
<pre class="hljs"><code>{
  <span class="hljs-attr">content</span>: <span class="hljs-string">'Pull Down To Refresh'</span>,
  <span class="hljs-attr">height</span>: <span class="hljs-number">60</span>,
  <span class="hljs-attr">autoRefresh</span>: <span class="hljs-literal">false</span>,
  <span class="hljs-attr">downContent</span>: <span class="hljs-string">'Pull Down To Refresh'</span>,
  <span class="hljs-attr">upContent</span>: <span class="hljs-string">'Release To Refresh'</span>,
  <span class="hljs-attr">loadingContent</span>: <span class="hljs-string">'Loading...'</span>,
  <span class="hljs-attr">clsPrefix</span>: <span class="hljs-string">'xs-plugin-pulldown-'</span>
}
</code></pre>
<p>pullup 默认配置:</p>
<pre class="hljs"><code>{
  <span class="hljs-attr">content</span>: <span class="hljs-string">'Pull Up To Refresh'</span>,
  <span class="hljs-attr">pullUpHeight</span>: <span class="hljs-number">60</span>,
  <span class="hljs-attr">height</span>: <span class="hljs-number">40</span>,
  <span class="hljs-attr">autoRefresh</span>: <span class="hljs-literal">false</span>,
  <span class="hljs-attr">downContent</span>: <span class="hljs-string">'Release To Refresh'</span>,
  <span class="hljs-attr">upContent</span>: <span class="hljs-string">'Pull Up To Refresh'</span>,
  <span class="hljs-attr">loadingContent</span>: <span class="hljs-string">'Loading...'</span>,
  <span class="hljs-attr">clsPrefix</span>: <span class="hljs-string">'xs-plugin-pullup-'</span>
}
</code></pre>
<p><span class="vux-qa">QA 如何更新数据</span></p>
<p>如果展示内容只是简单的增加或者减少，直接调用<code>reset</code>方法即可</p>
<pre class="hljs"><code><span class="hljs-keyword">this</span>.$nextTick(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
  <span class="hljs-keyword">this</span>.$refs.scroller.reset()
})
</code></pre>
<p>如果展示内容完全重载，那么需要主动设置位置让其能正确回到顶部。适用于改变筛选条件后重载数据的情况</p>
<pre class="hljs"><code><span class="hljs-keyword">this</span>.$nextTick(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
  <span class="hljs-keyword">this</span>.$refs.scroller.reset({
    <span class="hljs-attr">top</span>: <span class="hljs-number">0</span>
  })
})
</code></pre>
<p><span class="vux-qa">QA 如何设置pullup完成</span></p>
<p>方法1，直接调用ref的<code>donePullup</code>方法</p>
<pre class="hljs"><code><span class="hljs-keyword">this</span>.$refs.demo2.donePullup()
</code></pre>
<p>方法2，绑定value, 重置状态</p>
<p class="tip">自定义pullup模板同样是用v-model来绑定以获取状态变化</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">scroller</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"status"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">scroller</span>&gt;</span>
</code></pre>
<pre class="hljs"><code>data () {
  <span class="hljs-keyword">return</span> {
    <span class="hljs-attr">status</span>: {
      <span class="hljs-attr">pullupStatus</span>: <span class="hljs-string">'default'</span>
    }
  }
}
</code></pre>
<pre class="hljs"><code>// 重置状态为default
this.status.pullupStatus = 'default
</code></pre>
<p><span class="vux-qa">QA 如何设置pulldown完成</span></p>
<p>参照<code>pullup</code>, 使用方法<code>donePulldown</code>或者绑定<code>pulldownStatus</code>然后重置为<code>default</code>。</p>
<p><span class="vux-qa">QA 如何禁用或者启用pullup</span></p>
<p>在有些情况下，比如数据不多不需要上拉加载或者已经加载完成，我们需要禁用pullup<br>
同样可以调用方法 <code>disablePullup</code> 或者设置<code>pullupStatus</code>为<code>disabled</code></p>
<p>反之，则调用方法 <code>enablePullup</code> 或者设置<code>pullupStatus</code>为<code>enabled</code></p>
<p><span class="vux-qa">启用keep-alive后滚动有问题</span></p>
<p>需要在调用Scroller的页面上在 hook: <code>activated</code>上执行<code>reset</code></p>
<pre class="hljs"><code>activated () {
  <span class="hljs-keyword">this</span>.$refs.scroller.reset()
}
</code></pre>
