<h3>XIcon_COM</h3>
<p><span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/components/x-icon/metas.yml" target="_blank">编辑文档</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="#" router-link="/zh-CN/demos/x-icon">进入demo页面</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://vux.li/demos/v2/#/component/x-icon" target="_blank">demo 原始链接</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/demos/XIcon.vue" target="_blank">demo源码</a></span></p>
<pre class="hljs"><code><span class="hljs-keyword">import</span> { XIcon } <span class="hljs-keyword">from</span> <span class="hljs-string">'vux'</span>
</code></pre>
<p class="tip">
组件来自于 [Ionicons(MIT)](http://ionicons.com/)，你可以在网站上面看到所有的图标。
<br />
该组件无法在`script`引入的方式中使用，需要自行拷贝 svg 资源。
</p>
<p><code>x-icon</code> 是一个虚拟(占位)组件，你不需要引入<code>XIcon</code> 组件，当调用时<code>vux-loader@^1.0.43</code>会自动将转换成 <code>inline svg</code>。</p>
<p>你可以根据类名来设置样式(fill)。</p>
<p>目前并不支持 <code>svg sprite</code>，将在后面版本支持。</p>
<p>示例(请更新 vux-loader 到 v1.0.57 以上，否则不支持传除 type 和 size 之外的属性)：</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">x-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-arrow-up"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"icon-red"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">x-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">x-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-arrow-up"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"30"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">x-icon</span>&gt;</span>
</code></pre>
<p><span class="vux-props-title">Props</span></p>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>version</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">type</span></td>
<td><span class="type type-string">String</span></td>
<td></td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>图标名字</td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">size</span></td>
<td><span class="type type-string">String</span></td>
<td>24</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>尺寸大小</td>
</tr>
</tbody>
</table>
